<template>
    <div class="ztxq">
        <van-nav-bar
            title="专栏详情"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            @click-right="onClickRight"

        />
        <div  class="nr">
            <div>
            <h1>{{ztxq.title}}</h1>
            <img :src="ztxq.pic" alt="">
            </div>
            <div v-html="ztxq.content"></div>
        </div>
    </div>
</template>
<script>
export default {
    name:'ztxq',
    data() {
        return {
            ztxq:[]
        }
    },
    methods: {
        onClickLeft(){
            this.$router.go(-1)
        }
    },
    created() {
        console.log(this.$route.query.id)
        this.$axios.get('https://api.it120.cc/small4/cms/news/detail?id='+this.$route.query.id).then((res)=>{
            console.log(res.data.data)
            this.ztxq=res.data.data
        })
    },
}
</script>
<style lang="scss" scoped>
.ztxq{
     h1{
        font-size: 0.3rem;
        color: #333333;
        width: 100%;
        text-align: center;
        margin-top: 0.45rem;
        margin-bottom: 0.2rem;
    }
    p{
            font-size: 0.02rem;
            color: #333333;
            width: 100%;
            text-align: center;
            margin-top: 0.45rem;
            margin-bottom: 0.2rem;
            margin-left: .2rem;
            margin-right: .2rem;
            display: block;
            margin-block-start: 1em;
            margin-block-end: 1em;
            margin-inline-start: 0px;
            margin-inline-end: 0px
    }
    img{
        width: 100vw;
        display: block;
        margin: 0 auto;
    }
}
.nr{
    height: 90vh;
    overflow: auto;
}
</style>


