<template>
    <div class="zc">
        <h1 style="text-align:center">新用户注册</h1>
        <h6 style="text-align:center;color: gray; font-size:.14rem;" >很高兴您将成为商城的会员(注册只需一步)</h6>
        <!-- <div class="zc-content">
            <h2>新用户注册</h2>
            <h6>很高兴您将成为商城的会员(注册只需一步)</h6>
            <p>
                <input type="text" placeholder="手机号码" v-model="sjh">
            </p>
            <p>
                <input type="password" placeholder="密码">
            </p>
            <p>
                <input type="text" placeholder="用户名">
            </p>
            <p>
                <input type="text">
                <img :src="txyzm" alt="验证码"   >
            </p>
            <p>
                <input type="text"><button @click="hq()">获取验证码</button>
            </p>
                <button>立即注册</button>
            <p>
                <a href="已有账号？立即注册"></a>
            </p>
        </div> -->
        <van-form validate-first @failed="onFailed">
            <!-- 通过 pattern 进行正则校验 -->
            <van-field
                v-model="sjh"
                type="number"
                name="pattern"
                placeholder="请输入您要绑定的手机号"
                :rules="[{ pattern, message: '您的手机号输入有误' }]"
            />
            <!-- 通过 validator 进行函数校验 -->
            <van-field
                v-model="pwd"
                type="password"
                name="validator"
                placeholder="请输入您要设置的密码"
                :rules="[{ validator, message: '请输入正确密码' }]"
            />
            <van-field
                v-model="value2"
                type="password"
                name="validator"
                placeholder="请再次输入您要设置的密码"
                :rules="[{ validator, message: '您的密码输入不一致' }]"
            />

            <!-- 通过 validator 进行异步函数校验 -->
            <van-field
                v-model="value3"
                type="text"
                name="asyncValidator"
                placeholder="用户名"
                :rules="[{ validator: asyncValidator, message: '请输入正确内容' }]"
            />

            <img :src="txyzm" alt="" @click="hqyzm" />
            <van-field
                v-model="value4"
                type="text"
                name="yzmxy"
                placeholder="请输入图文验证码"
            />
            <!-- 获取短信验证码 -->
            <van-button type="default" @click="hq" v-show="flag">获取短信验证码</van-button>
            <p v-show="!flag">{{yzmsj}}秒后可再次验证</p>
            <van-field
                v-model="dxyzm"
                type="text"
                name="asyncValidator"
                placeholder="请输入您的短信验证码"
            />
            
            
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit" @click="zc()">
                注册
                </van-button>
            </div>

            <router-link to="/dl" style="font-size:.15rem;text-align:center;display:block;"> 已有账号，立刻登录</router-link>

        </van-form>
    </div>
</template>

<script>
import qs from 'qs'
export default {
    name:'zc',
    data() {
        return {
            txyzm:'https://api.it120.cc/small4/verification/pic/get',
            pwd:'',
            sjh:'',
            key:'',
            dxyzm:'',
            value1: '',
            value2: '',
            value3: '',
            value4:'',
            pattern: /^1[3456789]\d{9}$/,
            flag:true,
            yzmsj:60,
        }
    },
    methods: {
        hqyzm(){

            let a=Math.floor(Math.random()*999999);
            if (a<=111111) {
                a=Math.floor(Math.random()*999999);
            }
            this.key=a
            console.log(a)
            // this.sjs=a
            this.txyzm='https://api.it120.cc/small4/verification/pic/get?key='+a
        },

        hq(){
            this.flag=false
            let data={
                "mobile":this.sjh,
                "key":this.key,
                "picCode":this.value4
            }
            
            this.$axios.post('https://api.it120.cc/small4/verification/sms/get',qs.stringify(data)).then((res)=>{
                console.log(res)
            })

            var i=60;
            var timeId=setInterval(() => {
                i--;
                this.yzmsj=i;
                // console.log(i)
            }, 1000);
            setTimeout(()=>{
                alert("验证码超时")
                this.flag=true
                window.clearTimeout(timeId)
            },1000*60);
        },

        zc(){
            let data={
                "mobile":this.sjh,
                "pwd":this.pwd,
                "code":this.dxyzm
            }

            this.$axios.post('https://api.it120.cc/small4/user/m/register',qs.stringify(data)).then((res)=>{
                alert("注册成功")
            })

        }
    },
}
</script>
<style lang="scss" scoped>
    .zc{
        width: 90vw;
        height: 90vh;
        margin: 5vh auto;
        border: solid 1px grey;
        
    }
</style>


