import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        str:"hello",
        cart:[]
    },
    mutations:{
        gwcsj(state,obj){
            console.log(obj)
            state.cart.push(obj)
            // console.log(state.cart)
        },
        qx(state,che){
            state.cart.forEach((item,index)=>{
                    if (che) {
                        item.checked=true
                    }else{
                        item.checked=false
                    }
            })
        },
        sc(state){
            // alert("这是vuex中的删除按钮")
            state.cart=state.cart.filter((item)=>{
                return !item.checked
            })
        }

    },
    getters:{
        zj(state){
            var str=0;
            state.cart.forEach((item,index)=>{
                if (item.checked) {
                    str+=item.num*item.price
                }
            })
            console.log(str)
            return str
        }
    }
})


