<template>
    <div class="wd">
        <div class="wd-top">
            
                <img src="http://39.105.231.166:7002/static/toux.jpg" alt="">
                <p @click="dl()">{{str==""?"登录账号":str}}</p>
                <a href="#" @click="tc()">退出当前账号</a>
                <p>积分：0</p>
        </div>
        <div class="wd-ggw">
            <i class="van-icon van-icon-volume-o"><!----></i>
            <marquee behavior="" direction="left">新上线更稳定的付费快递查询接口</marquee>
        </div>
    </div>
</template>
<script>
export default {
    name:'wd',
    data() {
        return {
            str:localStorage.getItem('token')?"您已登陆成功":"请您登录"
        }
    },
    methods: {
        dl(){
            this.$router.push('/dl')
        },
        tc(){
            // alert("danji")
            localStorage.removeItem('token')
            this.$router.go(0)
        }
    },
}
</script>

<style lang="scss" scoped>
    .wd-top{
        width: 100%;
        height: 2.15rem;
        display: block;
        background: #C1B18F;
        
            display: flex;
            img{
                margin-top:.5rem;
                margin-left: .2rem;
                // display: block;
                width: 1.06rem !important;
                height: 1.06rem !important;
                border-radius: 50% !important;
            }
            p:nth-of-type(1){
                    font-size: 0.3rem;
                    color: white;
                    position: absolute;
                    left: 1.5rem;
                    top: 0.5rem;

            }
            p:nth-of-type(2){
                width: 1.24rem;
                padding: .1rem;
                color: #fff;
                background: #b3a078;
                border-radius: .0694444444rem;
                position: absolute;
                left: 1.5rem;
                top: 1.1rem;
                opacity: .8;
                font-size: 0.14rem;
            }
        
    }
    .wd-ggw{
            width: 100%;
            height: 0.6rem;
            background: white;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            color: #DB3B3B;
            border-bottom: 0.16rem solid #F5F5F5;
            marquee{
                    margin-left: 0.1rem;
                    font-size: 0.16rem;
                    width: 4.5rem;
            }
    }
</style>


