import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Sy from '@/components/SY/sy'
import ZTXQ from '@/components/SY/ztxq'
import FL from '@/components/FL/fl'
import GWC from '@/components/GWC/gwc'
import WD from '@/components/WD/wd'
import ZC from '@/components/WD/zc'
import DL from '@/components/WD/dl'
import FLDE from '@/components/FL/fldetile'
import FLSPXQ from '@/components/FL/flspxq'
import QRDD from '@/components/GWC/qrdd'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/hello'
    },
    {
      path:'/ztxq',
      name:'ztxq',
      component:ZTXQ
    },
    {
      path:'/zc',
      component:ZC
    },
    {
      path:'/dl',
      component:DL
    },
    {
      path:'/flde',
      component:FLDE
    },
    {
      path:'/flspxq',
      component:FLSPXQ
    },
    {
      path:'/qrdd',
      component:QRDD
    },
    {
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/hello/sy',
      children:[
        {
          path:'sy',
          component:Sy
        },
        {
          path:'fl',
          component:FL
        },
        {
          path:'gwc',
          component:GWC
        },
        {
          path:'wd',
          component:WD
        }
      ]
    },

  ]
})



