<template>
  <div class="flspxq">
      <!-- 分类商品详情页面 -->
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,key) in swiperlist" :key="key"><img :src="item.pic" alt=""></van-swipe-item>
     </van-swipe>
     <div style="border-bottom:solid .2rem #f0f0f0">
         <p>{{list.basicInfo.name}}</p>
         <p style="font-size:.1rem;color:gray;line-height:.6rem">{{list.basicInfo.characteristic}}</p>
         <p style=" font-size:.1rem; display:flex; justify-content: space-around; line-height:.2rem"><span style="color:red;">低价:{{list.basicInfo.minPrice}}</span> <s>原价:{{list.basicInfo.originalPrice}}</s> <span>库存:{{list.basicInfo.stores}}件</span></p>

     </div>
     <van-icon name="arrow-left" class="fh" @click="fh()"/>

     <div class="xxk">
         <p @click="qh(false)" :class="[qhxxk?'':'qh']">商品介绍</p>
         <p @click="qh(true)" :class="[qhxxk?'qh':'']">商品评价</p>
     </div>

     <div v-html='list.content' style="width:100vw;" class="spxq-content" v-show="!qhxxk">

     </div>
     <div class="sppj" v-show="qhxxk">
         <!-- <h1>商品评价页面</h1> -->
         <ul class="pj">
             <li v-for="(item,key) in pj" :key="key">

                 <div>
                        <div>
                        <p>123123</p>
                        <p>{{item.goods.goodReputationStr}}</p>
                        </div>
                        <p>{{item.goods.goodReputationStr}}</p>
                        <div>
                        <p>{{item.goods.dateReputation}}</p>
                        <p>选择规格: <span>{{item.goods.goodsName}}</span></p>
                        </div>
                </div>

             </li>
         </ul>
     </div>

      

      <van-popup v-model="show"
               closeable
               position="bottom">
      <div id="liebxq">

          <!-- 商品规格 -->
        <div>
            <img :src="list.basicInfo.pic" alt="">
            <div>
                <p>{{list.basicInfo.name}}</p>
                <p>￥:{{list.basicInfo.minPrice}}</p>
            </div>
        </div>
        <div>
            <div v-for="(item,index1) in sp_che" :key="item.id" id="spgg">
            <p>{{item.name}}</p>
            <p><span style="margin-top:.1rem;" v-for="(i,index2) in item.childsCurGoods" :key="i.id" @click="xzspgg(index1,index2)"  :class="[i.checked?'spgg_active':'']"   >{{i.name}}</span></p>
            </div>
        </div>
        <div>
            
        </div>
        <!-- justify-content: space-between; -->
        <div style="display:flex;justify-content:space-between; padding:.1rem .2rem;">
            <p>商品数量</p><van-stepper v-model="value" />
        </div>
        <van-button type="danger" style="width:100vw" @click="jrgwc()">加入购物车</van-button>
      </div>

    </van-popup>
            
           <van-goods-action>
                    <van-goods-action-icon icon="chat-o" text="客服" dot />
                    <van-goods-action-icon icon="cart-o" text="购物车" badge="5"  @click="tzgwc()"/> 
                    <van-goods-action-icon icon="shop-o" text="店铺" bage="12" />
                    <van-goods-action-button type="warning" text="加入购物车" />
                    <van-goods-action-button type="danger" @click="show=true" text="立即购买" />
            </van-goods-action>

     </div>
  
</template>

<script>

import { Toast } from 'vant'

export default {
    name:'flspxq',
    data() {
        return {
            list:[],
            swiperlist:[],
            show:false,
            qhxxk:false,
            value:1,
            pj:[],
            ps:[],
            spgg:[],
            sp_che:[]
        }
    },
    methods: {

        //跳转购物车
        tzgwc(){
            this.$router.push('/hello/gwc')
        },
        //加入购物车
        jrgwc(){
            var t = localStorage.getItem('token')
            if (t) {
            this.show==false
            //  alert('hello')

            var newlist=[]
            
            this.sp_che.forEach((item,index)=>{
                        console.log(item)
                        var newdata= item.childsCurGoods.filter((v)=>{
                            return v.checked
                        })
                        newlist=newlist.concat(newdata)
            })
            
            // console.log(newlist)
            var obj = {
                    pic: this.list.basicInfo.pic,
                    name: this.list.basicInfo.name,
                    pro: newlist,
                    price: this.list.basicInfo.minPrice,
                    num: this.value,
                    checked: false,
                    id: this.list.basicInfo.id
            }
            // console.log(obj)
            this.$store.commit("gwcsj",obj)
            this.show=false
            Toast.success('成功加入购物车');
            
            }else{
                alert('请您登录')
                this.$router.push('/dl')
            }



        },
        //切换
        qh(qhflag){
            this.qhxxk=qhflag
            if (this.qhxxk==true) {
                this.$axios.post('https://api.it120.cc/small4/shop/goods/reputation?goodsId='+this.$route.query.id).then((res)=>{
                    console.log(res.data.data)
                    this.pj=res.data.data
                })
            }
        },
        //返回
        fh(){
            this.$router.go(-1)
        },
        xzspgg(n1,n2){
            this.sp_che[n1].childsCurGoods.forEach((item,index)=>{
            if(index==n2){
                item.checked=true
                this.$set(this.sp_che[n1].childsCurGoods,index,item)
            }else{
                item.checked=false
                this.$set(this.sp_che[n1].childsCurGoods,index,item)
            }
            })
        }

    },
    created() {
        this.$axios.post('https://api.it120.cc/small4/shop/goods/detail?id='+this.$route.query.id).then((res)=>{
            console.log(res.data.data)
            this.list=res.data.data
            this.spgg=res.data.data
            this.swiperlist=res.data.data.pics

            var arr=this.list.properties
            
             arr.forEach((item)=>{
                  item.childsCurGoods.forEach((v)=>{
                    v.checked=false
                    // return v
                })
            })

            this.sp_che=arr

            console.log(arr)

        })
    },
}
</script>

<style lang="scss">

.spgg_active{
    border: solid 1px red !important;
    color: red;
    
}

.flspxq{
    width: 100vw;
    overflow: hidden;
}

#liebxq{
    
    >div:nth-of-type(1){
        display: flex;
        border-bottom: solid #f0f0f0 .2rem;
        img{
            width: 20%;
            margin: 5%;
        }
        div{
            flex: 1;
            p:nth-of-type(1){
                font-size: .16rem;
                line-height: .7rem;
                padding-left: .01rem;
            }
            p:nth-of-type(2){
                font-size: .14rem;
                color: red;
            }
        }
    }
    #spgg{
       padding: .1rem 0;
       p:nth-of-type(1){
           margin-left: .1rem;
           line-height: .5rem;
       }
       p:nth-of-type(2){
           display: flex;
           flex-wrap: wrap;
           span{
               padding: .05rem .1rem;
               border: 1px gray solid;
               font-size: .13rem;
               text-align: center;
               margin-left: .2rem;
               border-radius: .1rem;
           }
       }
       p:nth-of-type(3){
           margin-left: .1rem;
           line-height: .5rem;
       }
       p:nth-of-type(4){
           display: flex;
           flex-wrap: wrap;
           span{
               padding: .05rem .1rem;
               border: 1px gray solid;
               font-size: .13rem;
               text-align: center;
               margin-left: .2rem;
               border-radius: .1rem;
               
           }
       }
   }
}
.sppj{
    li {
      height: 1.54rem;
      padding: 0 0.2rem;
      border-bottom: 0.01rem solid #e9e9e9;
      display: flex;
      
      > i {
        font-size: 0.8rem;
        margin-top: 0.18rem;
      }
      > div {
        margin-left: 0.14rem;
        > div:nth-of-type(1) {
          display: flex;
          align-items: center;
          margin-top: 0.18rem;
          p:nth-of-type(1) {
            font-size: 0.16rem;
          }
          p:nth-of-type(2) {
            width: 0.48rem;
            height: 0.32rem;
            border-radius: 0.06rem;
            color: white;
            text-align: center;
            line-height: 0.32rem;
            background: #b7282e;
            font-size: 0.16rem;
            margin-left: 0.2rem;
          }
        }
        > p:nth-of-type(1) {
          font-size: 0.16rem;
          margin-top: 0.1rem;
        }
        > div:nth-of-type(2) {
          display: flex;
          margin-top: 0.2rem;
          > p {
            font-size: 0.12rem;
            color: #b2b2b2;
          }
          > p:nth-of-type(2) {
            margin-left: 0.25rem;
          }
        }
      }
    }
  

}
.fh{
    width: .3rem;
    height: .3rem;
    background: gainsboro;
    position: fixed;
    top: .1rem;
    left: .1rem;
    text-align: center;
    border: solid 1px gray;
    border-radius: 50%;
    font-size: .2rem;
    line-height: .3rem;
}
  .content {
    padding: 16px 16px 160px;
  }
.qh{
    border-bottom: 2px solid  red;
    color: red;
}
.xxk{
    display: flex;
    &>p{
        width: 50%;
        text-align: center;
        line-height: .5rem;
    }
}
.spxq-content{
    img{
        width: 100vw;
    }
    li{
        height: 0.47rem;
        line-height: 0.27rem;
        padding-left: 0.14rem;
        padding-right: 0.24rem;
    }
    .img-lazyloaded {
        width: 100vw !important;
        height: .4rem !important;
    }
    .img-lazyload{
        width: 100vw !important;
        height: 1rem !important;
    }
}
.my-swipe .van-swipe-item {
    img{
        width: 100vw;
    }
  }



</style>


