<template>
  <div class="hello">
    <section id="my-nr">
      <router-view></router-view>
    </section>
    <van-tabbar v-model="active" active-color="red">
      <van-tabbar-item icon="home-o" to="/hello/sy">首页</van-tabbar-item>
      <van-tabbar-item icon="qr" to="/hello/fl">分类</van-tabbar-item>
      <van-tabbar-item icon="cart-o" to="/hello/gwc">购物车</van-tabbar-item>
      <van-tabbar-item icon="user-o" to="/hello/wd">我的</van-tabbar-item>
    </van-tabbar>
  </div>
  
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#my-nr{
    height: 90vh;
    overflow: auto;
}
</style>



