<template>
    <div class="qrdd">
        <van-nav-bar
            title="确认订单"
            left-text=""
            left-arrow
            @click-left="onClickLeft"
            @click-right="onClickRight"
         />
         <van-contact-card
            :type="cardType"
            :name="currentContact.name"
            :tel="currentContact.tel"
            @click="showList = true"
         />
    </div>
</template>
<script>
import { ContactCard, ContactList, ContactEdit } from 'vant'
export default {
    name:"qrdd",
      data() {
            return {
            chosenContactId: null,
            editingContact: {},
            showList: false,
            showEdit: false,
            isEdit: false,
            list: [
                {
                name: '张三',
                tel: '13000000000',
                id: 0,
                },
            ],
        }
    }
}
</script>
<style lang="scss">
    
</style>


