<template>
    <div class="dl">
        <h1 >登录账号</h1>
        <h2>虾米-严选商城欢迎你</h2>
        <van-form @submit="onSubmit">
                <van-field
                    v-model="username"
                    name="用户名"
                    label="用户名"
                    placeholder="用户名"
                    :rules="[{ required: true, message: '请填写用户名' }]"
                />
                <van-field
                    v-model="password"  
                    type="password"
                    name="密码"
                    label="密码"
                    placeholder="密码"
                    :rules="[{ required: true, message: '请填写密码' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit" @click="dl()">
                    登录
                    </van-button>
                </div>
                
        </van-form>
        <router-link to="/zc" style="text-align:center;display:block;">还没有账号，立即注册</router-link>
    </div>
</template>
<script>
import qs from 'qs'
export default {
    name:"dl",
    data() {
        return {
            username: '',
            password: '',
        }
    },  
    methods: {
        dl(){
            // alert('你好')
            var data={
                "mobile":this.username,
                "pwd":this.password
            }
            // console.log(data)

            this.$axios.post("https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",qs.stringify(data)).then((res)=>{
                // console.log(res)
                if (res.data.msg=="success") {
                    // console.log(res.data.data.token)
                    localStorage.setItem('token',JSON.stringify(res.data.data.token))
                    this.$router.push('/hello/wd')
                    alert("登陆成功")
                    
                }else{
                    alert("用户名或密码有误,登陆失败")
                }
            })
        }
    },
}
</script>

<style lang="scss">
    .dl{
        width: 90vw;
        height: 90vh;
        margin: 0 auto;
        border: solid 1px gainsboro;
        margin-top: 5vh;
        color:gray;
        box-sizing: border-box;
        padding: 10vh 10vw;
        h2{
            font-size: .14rem;
        }
    }
</style>


