<template>
    <div class="sy">
        <div class="banner">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" >
                <van-swipe-item v-for="(item,index) in banner" :key="index"><img :src="item.picUrl" alt=""></van-swipe-item>
            </van-swipe>
            <ul id="nav">
                <li><img src="http://39.105.231.166:7002/static/img/cd1.7808faf.png" alt=""><p>签到</p></li>
                <li><img src="data:image/png;base64," alt=""><p>礼券</p></li>
                <li><img src="data:image/png;base64," alt=""><p>砍价</p></li>
                <li><img src="data:image/png;base64," alt=""><p>专栏</p></li>
            </ul>
        </div>
        <section>
            <p id="qmkj">   全民砍价  <van-icon name="arrow" /> </p>
            <ul id="spx">
                <li v-for="(item,key) in spx" :key="key">
                    <div class="spx-left">
                        <img :src="item.pic" alt="">
                    </div>
                    <div class="spx-right">
                        <h4>{{item.name}}</h4>
                        <p>{{item.characteristic==""?"此条无内容":item.characteristic}}</p>
                        <div>
                            <p>
                                ￥{{item.minPrice}}
                                <span :style="{color:'red'}">低价</span>
                            </p>
                            <p>
                                <s>{{item.originalPrice}}</s>
                                <span>原价</span>
                            </p>
                            <p>
                                {{item.kanjiaPrice}}
                                <span>限量</span>
                            </p>
                        </div>
                    </div>
                </li>
            </ul>
        </section>

        <p id="jxzt">精选专题 <van-icon name="arrow" /> </p>

        <div id="zt">
            <ul>
            <li v-for="(item,key) in zt" :key="key" @click="ztxq(item.id)">
                <img :src="item.pic" alt="">
                <p>{{item.title}}</p>
                <p>{{item.descript}}</p>
            </li>
            </ul>
        </div>
        
        <p id="jxzt">人气推荐 <van-icon name="arrow" /></p>

        <div class="tj">
            <ul>
                <li v-for="(item,key) in tj" :key="key" @click="spxq(item.id)">
                    <img :src="item.pic" alt="">
                    <p>{{item.name}}</p>
                    <p>{{item.characteristic==""?"暂无此内容":item.characteristic}}</p>
                    <p>￥{{item.minPrice}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:"sy",
    data() {
        return {
            banner:[],
            spx:{},
            zt:[],
            tj:[]
        }
    },
    methods: {
        spxq(id){
            // alert(id)
            this.$router.push('/flspxq?id='+id)
        },
        ztxq(id){
            this.$router.push({name:'ztxq',query:{id:id}})
        }
    },
    created(){
        this.$axios.get('https://api.it120.cc/small4/banner/list').then((res)=>{
            // console.log(res)
            this.banner=res.data.data
        })
        this.$axios.get('https://api.it120.cc/small4/shop/goods/kanjia/list').then((res)=>{
            // console.log(res.data.data.goodsMap)
            this.spx=res.data.data.goodsMap
            // let str= res.data.data.goodsMap.filter((res)=>{
            //     return res.minPrice==1
            // })
            
        })

        //专题列表数据
        this.$axios.get('https://api.it120.cc/small4/cms/news/list').then((res)=>{
            // console.log(res.data.data)
            this.zt=res.data.data
        })


        this.$axios.get('https://api.it120.cc/small4/shop/goods/list').then((res)=>{
            // console.log(res.data.data)
            
            let str= res.data.data.filter((res,key)=>{
                return key>=24
            })

            this.tj=str
        })
    }
}
</script>
<style lang="scss" scoped>
    .sy{
        width: 100%;
        .banner{
            position: relative;
            .my-swipe .van-swipe-item {
                width: 100%;
                img{
                    width: 100%;
                }
            }
            #nav{
                padding-top: .3rem;
                padding-bottom: .3rem;
                width: 100%;
                display: flex;
                position: absolute;
                bottom: 0;
                left: 0;
                background: white;
                height: 10vh;
                border-top-right-radius: .5rem;
                border-top-left-radius: .5rem;
                img{
                    width: .6rem;
                    height: .6rem;
                }
                li{
                    width: 25%;
                    text-align: center;
                }
            }
        }
        section{
            width: 100%;
            #qmkj{
                width: 100%;
                height: 10vh;
                font-size: .19rem;
                line-height: 10vh;
                text-align: center;
                font-weight: 100;
            }
            #spx{
                width: 100%;
                li{
                    border-top: solid 1px gainsboro;
                    width: 100%;
                    display: flex;
                    .spx-left{
                        width: 33%;
                        img{
                            display: block;
                            width: 1.2rem;
                            height: 1.2rem;
                            margin: .1rem;
                        }
                    }
                    .spx-right{
                        flex: 1;
                        line-height: .3rem;
                        h4{
                            margin-top: .2rem;
                            font-size: .16rem;
                            font-weight: 0;
                        }
                        p{
                            font-size: .15rem;
                            color: gray;
                        }
                        div{
                            display: flex;
                            color: gray;
                            line-height: .2rem;
                            text-align: center;
                            p{
                                margin-right: .5rem;
                                span{
                                    display: block;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

           #jxzt{
                width: 100%;
                height: 10vh;
                font-size: .19rem;
                line-height: 10vh;
                text-align: center;
                font-weight: 100;
            }
#zt{
    width: 100vw;
    overflow: auto;
    ul{
        display: flex;
        li{
            img{
                width: 80vw;
                height: 2rem;
            }
            p:nth-of-type(1){
                width: 3rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow:ellipsis;
            }
            p:nth-of-type(2){
                width: 3rem;
                color: gray;
                overflow: hidden;
                text-overflow:ellipsis;
                font-size: .12rem;
                white-space: nowrap;
            }
        }
    }
}


.tj{
    width: 100vw;
    ul{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        li{
            width: 50%;
            img{
                width: 90%;
                height: 2rem;
                display: block;
                margin: 0 auto;
            }
            p:nth-of-type(1){
                font-size: .15rem;
                padding-left: .12rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow:ellipsis;

            }
            p:nth-of-type(2){
                white-space: nowrap;
                color: grey;
                line-height: .3rem;
                padding-left: .12rem;
                font-size: .1rem;
                overflow: hidden;
                text-overflow:ellipsis;

            }
            p:nth-of-type(3){
                padding-left: .12rem;
                font-size: .13rem;
                color: red;
            }
        }
    }
}
</style>


