<template>
    <div class="gwc">

        <van-nav-bar
            title="购物车"
            :left-text="bj?'完成':'编辑'"
            @click-left="onClickLeft"
        />
    
    <div class="content">
        <ul id="gwc">
        <li v-for="(item,index) in $store.state.cart" :key="index">
            <!-- <input type="checkbox" v-model="item.checked" /> -->
            <van-checkbox v-model="item.checked"></van-checkbox>
            <img :src="item.pic" alt="">

            <div>
                <p id="spm">{{item.name}}</p>
                <p><span v-for="(v,i) in item.pro" :key="i"  id="spgg"> {{v.name}} </span></p>
                <p><span style="color:red">￥:{{item.price}}</span> <van-stepper  v-model="item.num" theme="round" button-size="22" disable-input /></p>
            </div>

            <!-- {{$store.state.cart}} -->

        </li>
        </ul>
    </div>
    <van-submit-bar  :button-text="bj?'删除':'下单'" @submit="onSubmit">

            <van-checkbox  v-model="che" @click="qx()">全选</van-checkbox>
            <span class="van-submit-bar__text span">总价:</span>
            <span class="van-submit-bar__price" style="font-size:.21rem;">{{$store.getters.zj}}</span>
     </van-submit-bar>
    </div>
</template>
<script>
export default {
    name:'gwc',
    data() {
        return {
            che:false,
            bj:false
        }
    },
    methods: {
        // 全选
        qx(){
                // alert('true')
                // alert(this.che)
                this.$store.commit("qx",this.che)    
        },
        // 编辑
        onClickLeft(){
            this.bj=!this.bj
        },
        onSubmit(){
            // alert('你好')
            if (this.bj) {
                // alert("这是删除按钮")
                this.$store.commit("sc")
            }else{
                // alert('这是下单按钮')
                this.$router.push('/qrdd')
                

            }
        }
    },
}
</script>

<style lang="scss">

#spgg{
    color: gray;
    font-size: .1rem;
    
}

.van-submit-bar{
    bottom:10vh !important;
}
.content{
    width: 100vw;
    overflow: auto;
    box-sizing: border-box;
    #spm{
        text-overflow: ellipsis;
        
        
    }
}

.gwc{
    width: 100vw;
    #gwc{
    li{
     border-top: solid .05rem #f0f0f0;
     display: flex;
     align-items: center;
     input{
         width: .2rem;
         height: .2rem;
         border-radius: 50%;
         margin: .1rem;
     }
     img{
         width: 20vw;
         margin: .2rem;

     }
     
     >div:nth-of-type(2){
         line-height: .4rem;
         flex: 1;
         p:nth-of-type(3){
             display: flex;
             justify-content: space-around;
         }
     }
    }
    }
}
</style>


