<template>
  <div class="fldetile">
      <van-nav-bar
        title="标题"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
       />

       <ul class="splb">
           <li v-for="(item,key) in list" :key="key" @click="spxq(item.id)">
               <img :src="item.pic" alt="">
               <p>{{item.name}}</p>
               <p>￥{{item.minPrice}}</p>
           </li>
       </ul>
  </div>
</template>

<script>
export default {
    name:'fldetile',
    data() {
        return {
            list:[]
        }
    },
    methods: {
        onClickLeft(){
            this.$router.go(-1)
        },
        spxq(id){
            // alert(id)
            this.$router.push('/flspxq?id='+id)
        }
    },
    created() {
        this.$axios.get('https://api.it120.cc/small4/shop/goods/list').then((res)=>{
            console.log(res)
            console.log(this.$route.query.id)
            this.list= res.data.data.filter((res)=>{
                return res.categoryId==this.$route.query.id
            })
            // console.log(b)
        })
        
    },
}
</script>

<style lang="scss">
.splb{
    width: 100vw;
    display: flex;
    flex-wrap: wrap;

    li{
        width: 50%;
        position: relative;
        img{
            width: 100%;
            height: 2.8 rem;
        }
        p:nth-of-type(1){
                position: absolute;
                left: 0;
                top: 2rem;
                width: 100%;
                height: 0.4rem;
                background: #F0EDD4;
                font-size: 0.14rem;
                color: #B69B72;
                text-indent: 0.02rem;
                line-height: 0.4rem;
        }
        p:nth-of-type(2){
                font-size: 0.2rem;
                color: #CF1311;
                position: absolute;
                left: 0;
                top: 2.5rem;
        }
    }
}

</style>


