<template>
    <div class="fl">
        <header>
            <input type="text" placeholder="回车搜索商品">
        </header>
        <section>
            <div class="fl-left">
               
            <ul>
                <li :class="[active==0?'active':'']" @click="djqh(0,1)">所有商品</li>
                <li v-for="(item,key) in yjfl" :key="key" :class="[active==key+1?'active':'']" @click="djqh(key+1,item.id)">{{item.name}}</li>
            </ul>
            </div>
            <div class="fl-right">
                 <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                    <van-swipe-item><img src="/static/timg (1).jpg" alt=""></van-swipe-item>
                    <van-swipe-item><img src="/static/timg.jpg" alt=""></van-swipe-item>
                    <van-swipe-item><img src="/static/timg (2).jpg" alt=""></van-swipe-item>
                </van-swipe>
                <ul>
                    <li v-for="(item,key) in qbsp" :key="key" @click="spxq(item.id)">
                        <img :src="item.icon" alt="">
                        <p>{{item.name}}</p>
                    </li>
                </ul>
            </div>
        </section>
    </div>
</template>
<script>
export default {
    name:'fl',
    data() {
        return {
            yjfl:[],
            qbsp:[],
            newqbsp:[],
            active:0
        }
    },
    methods: {
         djqh(id,cid){
            this.active=id
            // alert(cid)
            this.qbsp=this.newqbsp
            var newqb = this.qbsp.filter((res,key)=>{
                  return res.pid==cid
               
               })
            if (cid==1) {
                this.qbsp=this.newqbsp
            }else{
                this.qbsp=newqb
            }
        },
        spxq(id){
            this.$router.push('/flde?id='+id)
        }
    },
    created() {
        this.$axios('https://api.it120.cc/small4/shop/goods/category/all').then((res)=>{
            this.qbsp=res.data.data
            this.newqbsp=res.data.data
            var yjfl = res.data.data.filter((v,i)=>{
                return v.level==1
            })

            this.yjfl=yjfl
            // console.log(yjfl)
        })
    },
}
</script>

<style lang="scss" scoped>
header{
    width: 100vw;
    height: 10vh;
    display: flex;
    align-items: center;
    input{
        height: .3rem;
        font-size: .1rem;
        padding-left: .4rem;
        margin-left: .4rem;
        background: #f0f0f0;
        border: none;
        border-radius: .2rem;
    }
}
    section{
        display: flex;
        width: 100vw;
        
        .fl-left{
            width: 30vw;
            height: 80vh;
            // background: blanchedalmond;
            border: solid 1px gainsboro;
            ul{
                width: 100%;
                display: flex;
                flex-direction: column;
                li{
                    line-height: .6rem;
                    text-align: center;
                }
            }
        }
        .fl-right{
            font-size: .14rem;
            font-weight: 800;
            flex: 1;
            height: 80vh;
            // background: blue;
            overflow: auto;
            border: solid 1px gainsboro;
            ul{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;
                width: 100%;
                li{
                    width: 30%;
                    height: 1rem;
                    img{
                        width: 70%;
                        height: 60%;
                        margin: 0 auto;
                        display: block;
                    }
                    p{
                        text-align: center;
                        line-height: .3rem;
                    }
                }
            }
        }
        
    }


    .active{
        color: red;
    } 
.my-swipe .van-swipe-item {
    width: 100%;
    img{
        width: 100%;
        height: 1.5rem;
    }
  }
</style>


